.spinnerContainer{
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  
}

@keyframes spinner {
  to {transform: rotate(360deg);}
}
 
.spinner {
  content: '';
  box-sizing: border-box;

  width: 30vw;
  height: 30vw;
  border-radius: 50%;
  border: 3px solid #f6f;
  border-top-color: #0e0;
  border-right-color: #0dd;
  border-bottom-color: #f90;
  animation: spinner .6s linear infinite;
}


.inputLetter {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 2px solid black;
  text-align: center;
  margin: .5em;
}

.row {
  display: flex;
}

.correct {
  background-color: green;
}

.wspot {
  background-color: yellow;
}

.wrong {
  background-color: red;
}

.letters {
  width: 90vw;
  max-width: 300px;
  display: grid;
  padding: 5px;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(3, 1fr);
  font-size: 8px;
  grid-template-areas: 
    "Q W E R T Y U I O P"
    "A S D F G H J K L ."
    "Z X C V B N M . . .";
}

.letters * {
  place-self: center;
  padding: 0;
}



